<template>
  <div id="assets">
    <section class="section">
      <h1 class="is-size-4 has-text-left">Purchased Movies</h1>
      <div class="horizontal-scroll-wrapper has-text-left">
        <router-link
          v-for="m in getMoviePurchases"
          :to="`/assets/${m.id}`"
          :key="m.id"
        >
          <img
            v-if="m.images"
            :src="filterImage(m.images)[0].url + '?im=Resize,height=200'"
          />
        </router-link>
      </div>
      <h1 class="is-size-4 has-text-left">Rented Movies</h1>
      <div class="horizontal-scroll-wrapper has-text-left">
        <router-link
          v-for="m in getMovieRentals"
          :to="`/assets/${m.id}`"
          :key="m.id"
        >
          <img
            v-if="m.images"
            :src="filterImage(m.images)[0].url + '?im=Resize,height=200'"
          />
        </router-link>
      </div>
      <h1 class="is-size-4 has-text-left">Wishlist - Movies</h1>
      <div class="horizontal-scroll-wrapper has-text-left">
        <router-link
          v-for="m in getWishList.movies"
          :to="`/assets/${m.id}`"
          :key="m.id"
        >
          <img :src="filterImage(m.images)[0].url + '?im=Resize,height=200'" />
        </router-link>
      </div>
      <h1 class="is-size-4 has-text-left">Wishlist - TV Shows</h1>
      <div class="horizontal-scroll-wrapper has-text-left">
        <router-link
          v-for="m in getWishList.series"
          :to="`/assets/${m.id}`"
          :key="m.id"
        >
          <img :src="filterImage(m.images)[0].url + '?im=Resize,height=200'" />
        </router-link>
      </div>

      <router-view></router-view>
    </section>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Assets",
  data() {
    return {
      wishlist: {},
      librarylist: {},
      movieRentals: [],
    };
  },
  computed: mapGetters(["getWishList", "getMovieRentals", "getMoviePurchases"]),
  created() {
    let authData = JSON.parse(localStorage.getItem("authData"));
    this.fetchWishList({ authData });
    this.fetchMovieRentals({ authData });
  },
  $route: {
    immediate: true,
    handler: function (newVal) {
      this.showModal = newVal.meta && newVal.meta.showModal;
    },
  },
  methods: {
    filterImage(images) {
      let i = images.filter((c) => c.type === "SLICK");
      return i;
    },
    ...mapActions(["fetchWishList", "fetchMovieRentals"]),
  },
};
</script>

