<template>
  <section class="hero is-primary is-fullheight">
    <div class="hero-body">
      <div class="container">
        <div class="columns is-centered">
          <div class="column is-5-tablet is-4-desktop is-3-widescreen">
            <!--form action="" class="box"-->
            <div class="box">
              <div class="field">
                <label for="" class="label">Email</label>
                <div class="control has-icons-left">
                  <input
                    type="email"
                    name="email"
                    v-model="input.email"
                    placeholder="e.g. bobsmith@gmail.com"
                    class="input"
                  />

                  <span class="icon is-small is-left">
                    <i class="fa fa-envelope"></i>
                  </span>
                </div>
              </div>
              <div class="field">
                <label for="" class="label">Password</label>
                <div class="control has-icons-left">
                  <input
                    type="password"
                    name="password"
                    v-model="input.password"
                    placeholder="Password"
                    class="input"
                  />
                  <span class="icon is-small is-left">
                    <i class="fa fa-lock"></i>
                  </span>
                </div>
              </div>
              <div class="field">
                <label for="" class="checkbox">
                  <input type="checkbox" />
                  Remember me
                </label>
              </div>
              <div class="field">
                <button class="button is-success" v-on:click="login()">
                  Login
                </button>
              </div>
            </div>
            <!--/form-->
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "Login",
  data() {
    return {
      input: {
        email: "",
        password: "",
      },
    };
  },
  methods: {
    ...mapActions(["fetchWishList", "fetchMovieRentals"]),

    getAuthToken(artifactValue) {
      return new Promise((resolve, reject) => {
        var AuthHeaders = new Headers();
        AuthHeaders.append("Authorization", `Basic ${this.$parent.raaAuth}`);
        AuthHeaders.append("Content-Type", "application/x-www-form-urlencoded");

        var urlencoded = new URLSearchParams();
        urlencoded.append("raa_token", artifactValue);
        urlencoded.append("grant_type", "raa_token");

        var requestOptions = {
          method: "POST",
          headers: AuthHeaders,
          body: urlencoded,
          redirect: "follow",
        };

        fetch(
          "https://hfozhlnmfh.execute-api.ap-southeast-2.amazonaws.com/v1/auth/tokens",
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
    signIn() {
      return new Promise((resolve, reject) => {
        var AuthHeaders = new Headers();
        AuthHeaders.append("Authorization", `Basic ${this.$parent.basicAuth}`);

        var requestOptions = {
          method: "POST",
          headers: AuthHeaders,
          redirect: "follow",
        };
        fetch(
          `https://services.bigpond.com/rest/v1/AuthenticationService/authenticate?authToken=${this.input.password}&userIdentifier=${this.input.email}&userIdentifierType=EMAIL`,
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
    async login() {
      try {
        let result = await this.signIn();
        let token = await this.getAuthToken(result.data.artifactValue);

        console.log(token);
        if (token.access_token) {
          localStorage.setItem("authData", JSON.stringify(token));
          this.$parent.authenticated = true;
          this.fetchMovieRentals({ authData: token });

          this.$router.replace({ name: "Assets" });
        }
      } catch (error) {
        this.$parent.authenticated = false;
      }
    },
  },
};
</script>

<style>
.hero.is-primary {
  background-color: #3f4645;
  color: #fff;
}
.button.is-success {
  background-color: #7f2e2d;
  border-color: transparent;
  color: #fff;
}
