<template>
  <div
    v-if="showModal && asset.images"
    class="is-active modal"
    :style="'--hero-image: url(' + asset.images[0].url + ');'"
  >
    <div class="modal-background"></div>
    <div class="modal-content">
      <section class="hero">
        <div class="hero-body">
          <div class="container is-fullhd">
            <h1 class="has-text-light is-size-1 title has-text-left">
              {{ asset.displayTitle }}
            </h1>
            <h2 class="has-text-light is-size-5 sub-title has-text-left">
              {{ asset.descriptionShort }}
            </h2>
            <div class="pt-5 has-text-left">
              <button
                v-if="asset.availability"
                class="button is-white mr-4"
                @click="
                  $router.push({
                    name: 'Watch',
                    params: {
                      embedCode: asset.availability.embedCode,
                      posterImg: asset.images[0].url,
                    },
                    query: { offerId: asset.availability.offerId },
                  })
                "
              >
                Watch
              </button>
              <button
                v-if="asset.trailer"
                class="button is-white"
                @click="
                  $router.push({
                    name: 'Watch',
                    params: {
                      embedCode: asset.trailer.embedCode,
                      posterImg: asset.images[0].url,
                    },
                  })
                "
              >
                Watch Trailer
              </button>
            </div>
            <!--img :src="asset.images[0].url" /-->
          </div>
        </div>
      </section>
    </div>
    <button
      class="modal-close is-large"
      aria-label="close"
      v-on:click="closeModal()"
    ></button>
  </div>
</template>

<style>
.modal-content {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    var(--hero-image);
  background-size: cover;
}
</style>
<script>
export default {
  props: ["assetId"],
  data() {
    return {
      asset: Object,
      showModal: false,
      authData: null,
    };
  },
  watch: {
    $route(to, from) {
      this.asset = to.params.assetId;
      this.getAsset(this.assetId, this.authData).then((asset) => {
        this.asset = asset[0];
        let availability = asset[0].availability.filter((i) => {
          return i.isEntitled;
        });
        this.asset.availability = availability[0];
      });

      this.showModal = to.meta && from.meta.showModal;

      // react to route changes...
    },
  },
  mounted() {
    this.authData = JSON.parse(localStorage.getItem("authData"));

    this.asset = this.assetId;
    this.getAsset(this.assetId, this.authData).then((asset) => {
      this.asset = asset[0];
      let availability = asset[0].availability.filter((i) => {
        return i.isEntitled;
      });
      this.asset.availability = availability[0];
    });
    this.showModal = true;
  },
  methods: {
    closeModal() {
      this.showModal = false;
    },
    getAsset(assetId, authData) {
      return new Promise((resolve, reject) => {
        var AuthHeaders = new Headers();
        AuthHeaders.append("Authorization", `Bearer ${authData.access_token}`);
        AuthHeaders.append("Content-Type", "application/json");

        var requestOptions = {
          method: "GET",
          headers: AuthHeaders,
          redirect: "follow",
        };
        fetch(
          `https://hfozhlnmfh.execute-api.ap-southeast-2.amazonaws.com/v1/ttvbo/assets/${assetId}?attributes=*&uuid=${authData.uuid}`,
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
  },
};
</script>