<template>
  <div class="watch">
    <div
      class="poster"
      :style="'background-image: url(' + posterImg + ');'"
    ></div>
    <div class="overlay">
      <div class="backbutton">
        <arrow-left-icon
          size="2x"
          class="icons"
          @click="$router.push({ path: '/assets' })"
        ></arrow-left-icon>
      </div>
      <div class="is-size-4 has-text-left title">
        {{ title }}
      </div>
      <div class="is-size-6 has-text-left error has-text-danger">
        {{ errorTitle }}
      </div>
    </div>

    <video
      class="video"
      id="player"
      crossOrigin="anonymous"
      :poster="posterImg"
      controls
    ></video>
  </div>
</template>

<script>
import clpp from "../castlabs/cl.core.min.js";
import "../castlabs/cl.mse.min.js";
import "../castlabs/cl.dash.min.js";
import "../castlabs/cl.hls.min.js";
import "../castlabs/cl.crypto.min.js";
import jwt_decode from "jwt-decode";
import { ArrowLeftIcon } from "vue-feather-icons";

export default {
  components: {
    ArrowLeftIcon,
  },
  props: ["embedCode", "posterImg"],
  data() {
    return {
      currentTime: null,
      lastPing: -1,
      title: "",
      errorTitle: "",

      pingInterval: 300,
      eventNumber: 1,
      duration: 0,
      firstPlay: true,
      player: null,
      profileRank: {
        uhd: 1,
        fhd: 2,
        hd: 3,
        sd: 4,
      },
      drmType: "drmtoday.widevine",
      contentType: "DASH-H264",
    };
  },
  async mounted() {
    this.authData = JSON.parse(localStorage.getItem("authData"));

    let isSafari = window.safari !== undefined;

    if (isSafari) {
      this.drmType = "drmtoday.fairplay";
      this.contentType = "HLS";
    }

    let license = this.$parent.clPlayerLicense;
    this.player = new clpp.Player("player", { license });
    this.player.use(clpp.dash.DashComponent);
    this.player.use(clpp.hls.HlsComponent);
    this.player.use(clpp.crypto.CryptoComponent);
    console.log("watch initiated");
    try {
      let playerData = await this.initatePlayer();
      let options = this.getPlayerOptions(playerData);
      console.log(options);
      this.player.load({
        ...options,
        autoplay: true,
      });
    } catch (err) {
      console.log(err);
    }
  },
  beforeDestroy() {},
  methods: {
    getPlayerOptions(playerData) {
      let url, type, options;

      if (!playerData.data[0].isDrmProtected) {
        url = playerData.data[0].alternativeStreams[0].manifest.uri;
        type = playerData.data[0].alternativeStreams[0].mimeType;
        options = {
          source: {
            url,
            type,
          },
        };
      } else {
        let filteredStream = playerData.data[0].alternativeStreams.filter(
          (i) => i.drmProxyType === this.drmType
        )[0];
        url = filteredStream.manifest.uri;
        type = filteredStream.mimeType;
        let drmHeaders = filteredStream.drm.headers;
        var token = drmHeaders["x-dt-auth-token"];
        var optData = JSON.parse(jwt_decode(token).optData);
        options = {
          source: {
            url,
            type,
          },
          drm: {
            env: "DRMtoday",
            customData: {
              userId: optData.userId,
              sessionId: optData.sessionId,
              merchant: optData.merchant,
              authToken: token,
              assetId: `ttvbo-${playerData.data[0].assetId}`,
            },
          },
        };
      }
      return options;
    },
    async initatePlayer() {
      try {
        let { embedToken } = await this.getOoyalaToken({
          embedCode: this.$route.params.embedCode,
        });

        let { token, assetId } = await this.getJWT({
          OoyalaToken: embedToken,
          embedCode: this.$route.params.embedCode,
          deviceType: "TTV",
          deviceModel: "Model",
          osVersion: "OS Version",
          security: {
            playready: {
              level: "default",
            },
          },
        });
        let playerData = await this.getPlayUrl({ token, assetId });
        this.title = playerData["data"][0].assetTitle;
        playerData["data"][0].alternativeStreams.push(
          playerData["data"][0].recommendedStream
        );
        playerData["data"][0].alternativeStreams.sort(this.compareProfileRank);
        return playerData;
      } catch (e) {
        this.errorTitle = "ASSET NOT FOUND";
      }
    },
    getPlayUrl({ assetId, token }) {
      return new Promise((resolve, reject) => {
        var AuthHeaders = new Headers();
        AuthHeaders.append("Content-Type", "application/json");
        AuthHeaders.append("Authorization", `Bearer ${token}`);
        var raw = JSON.stringify({
          device: {
            label: "SANDPIT",
            udid: this.$parent.authData.uuid,
          },
        });

        var requestOptions = {
          method: "POST",
          headers: AuthHeaders,
          body: raw,
          redirect: "follow",
        };

        fetch(
          `https://ttvbo.play.cf.ap-southeast-2-prod.vmnd.tv/api/v2/asset/${assetId}/play?extraFields=alternativeStreams&contentType=${this.contentType}`,
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
    getJWT(options) {
      return new Promise((resolve, reject) => {
        var myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");

        var raw = JSON.stringify(options);

        var requestOptions = {
          method: "POST",
          headers: myHeaders,
          body: raw,
          redirect: "follow",
        };

        fetch(
          `https://ttvbo.mediaservices.com.au/users/${this.authData.uuid}/playbackInit`,
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
    compareProfileRank(left, right) {
      return (
        this.profileRank[left.urlProfile] - this.profileRank[right.urlProfile]
      );
    },
    getOoyalaToken({ embedCode }) {
      return new Promise((resolve, reject) => {
        var AuthHeaders = new Headers();
        AuthHeaders.append(
          "ovs-token",
          `${this.$parent.authData.access_token}`
        );
        AuthHeaders.append("Content-Type", "application/json");
        AuthHeaders.append("ovs-uuid", `${this.authData.uuid}`);
        AuthHeaders.append("bpm-environment", "bpm");

        var requestOptions = {
          method: "GET",
          headers: AuthHeaders,
          redirect: "follow",
        };
        fetch(
          `https://hfozhlnmfh.execute-api.ap-southeast-2.amazonaws.com/v2/bpm/ovs/users/${this.authData.uuid}/ooyalaToken?deviceType=ios&embedCode=${embedCode}`,
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
  },
};
</script>

